import { ErrorAccessor } from './ErrorAccessor';
import { resolveFromMapping } from '../util/resolveFromMapping';
import {
  OnlineManager,
  ErrorCodesMap,
  ShowErrorProps,
} from '@wix/error-handler-core/types';
import { CommonErrorsMapState } from './CommonErrorsMapState';

export interface ErrorResolutionParams {
  readonly errorAccessor: ErrorAccessor;
  readonly errorCodesMap: ErrorCodesMap | null | undefined;
  readonly commonErrorsMapState: CommonErrorsMapState;
  readonly onlineManager: OnlineManager;
}

export class ErrorResolution {
  readonly errorAccessor;
  readonly errorCodesMap;
  readonly commonErrorsMapState;
  readonly onlineManager;

  consumerResolvedError?: ShowErrorProps | null;
  commonResolvedError?: ShowErrorProps | null;

  constructor(params: ErrorResolutionParams) {
    this.commonErrorsMapState = params.commonErrorsMapState;
    this.errorAccessor = params.errorAccessor;
    this.errorCodesMap = params.errorCodesMap;
    this.onlineManager = params.onlineManager;
  }

  get commonErrors() {
    return this.commonErrorsMapState.commonErrors;
  }

  _resolveNetworkError() {
    const { onlineManager, commonErrors, errorAccessor } = this;

    if (!commonErrors) {
      return null;
    }

    if (onlineManager.onLine) {
      return commonErrors.getNetworkErrorShowErrorProps(errorAccessor);
    }

    return commonErrors.connectionErrorShowErrorProps;
  }

  _resolveCommonError() {
    const { errorAccessor, commonErrors, errorCodesMap } = this;

    const { error, httpError, validationError } = errorAccessor;

    if (httpError == null) {
      return this._resolveNetworkError();
    }

    if (!commonErrors) {
      return null;
    }

    if (validationError) {
      return commonErrors.commonErrorsMap.statusCodeError?.INVALID_ARGUMENT?.(
        error,
      );
    }

    const resolvedError = resolveFromMapping({
      errorAccessor,
      errorCodesMap: commonErrors.commonErrorsMap,
    });

    if (!resolvedError) {
      return (
        errorCodesMap?.serverError?.(error) ??
        commonErrors.defaultErrorShowErrorProps
      );
    }

    return resolvedError;
  }

  _resolveConsumerError() {
    const { errorCodesMap, errorAccessor } = this;

    const { httpError } = errorAccessor;

    if (httpError == null || errorCodesMap == null) {
      return null;
    }

    return resolveFromMapping({
      errorAccessor,
      errorCodesMap,
    });
  }

  resolve() {
    this.consumerResolvedError = this._resolveConsumerError();

    // If consumer resolved the error, we don't need to resolve the common error
    if (this.consumerResolvedError != null) {
      return;
    }

    this.commonResolvedError = this._resolveCommonError();
  }

  get errorToShow() {
    return this.consumerResolvedError ?? this.commonResolvedError;
  }
}
